<template>
  <div class="about">
    <div v-if="mostrarConteo">
      <h1>La gran incógnita va a ser revelada en:</h1>
      <h1>{{counter}}</h1>
    </div>

    <div v-else>
      <div v-if="mostrarOpciones">
        <h1>¿Quieres ser mi novia?</h1>
        <input type="button" @click="acepta()" value="Sí">
        <input type="button" @click="moverBoton($event)" value="No :c">
      </div>
      <div v-else>
        <h1>Te amo mucho uwu</h1>
        <iframe width="100%" height="300" src="https://www.youtube.com/embed/Yb-hJOmm22k" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      counter: 3,
      intervalCounter: null,
      mostrarConteo: true,
      mostrarOpciones: true
    }
  },

  mounted: function () {
    this.cuentaRegresiva()
  },

  methods: {

    cuentaRegresiva () {
      const that = this
      if (this.intervalCounter == null) {
        this.intervalCounter = setInterval(function () { that.cuentaRegresiva() }, 1000)
      }
      if (this.counter != 0) {
        that.counter -= 1
      } else {
        clearInterval(this.intervalCounter)
        this.mostrarConteo = false
      }
    },

    moverBoton (boton) {
      const x = (Math.random() * window.screen.width)
      const y = (Math.random() * window.screen.height)
      const stringStyle = 'position: absolute;top: ' + y + 'px; left: ' + x + 'px'
      boton.path[0].style = stringStyle
    },

    acepta () {
      this.mostrarOpciones = false
    }
  }
}
</script>

<style>
input[type = button]{
  width: 100px;
  height: 50px;
  margin: 10px

}
</style>
